}
.block-title {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin: 0 0 25px;
}
.block-title h2 {
  font-weight: 600;
  font-size: 30px;
  margin: 0;
}
.block-title .more {
  background-color: #4bc4d7;
  color: #fff;
  border-radius: 1000px;
  margin-left: 20px;
  font-size: 14px;
  font-weight: 400;
  padding: 10px 15px;
}
.page-title {
  margin: 8px 0 30px;
}
.page-title h1 {
  font-size: 40px;
  font-weight: 600;
  margin: 0;
}
.page-title_cart {
  display: flex;
  align-items: flex-start;
}
.page-title_cart h1 {
  position: relative;
}
.page-title_cart h1:after {
