  font-weight: 400;
  position: relative;
  margin-left: auto;
  margin-top: 18px;
  text-decoration: underline;
}
.page-title_cart .delete-btn .icon {
  position: absolute;
  left: -28px;
  top: -2px;
  background-color: #fbe6db;
  width: 21px;
  height: 21px;
  border-radius: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.page-title_cart .delete-btn svg {
  width: 9px;
  height: 8px;
  fill: #2c2d2b;
}
.header {
  border-bottom: 1px solid #d5d5d5;
  position: relative;
}
.header__top {
  display: flex;
  align-items: center;
  padding: 14px 0 0;
}
.header .nav {
  display: flex;
  align-items: center;
  padding-right: 25px;
  border-right: 2px solid #808180;
}
.header .nav__item {
  border-radius: 100px;
  font-weight: 300;
  font-size: 14px;
  transition: 0.3s;
}
.header .nav__item:hover {
  color: #f68a53;
}
.header .nav__item:not(:last-of-type) {
  margin-right: 21px;
}
.header .nav__item.active {
  color: #f68a53;
  position: relative;
}
.header .nav__item.active:before {
  border-radius: 1000px;
  content: '';
  position: absolute;
  left: -10px;
  top: -6px;
  width: calc(100% + 20px);
  height: calc(100% + 12px);
  border: 1px solid #f68a53;
  z-index: -1;
}
.header .contacts {
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: 25px;
}
.header .contacts .phone {
  font-size: 14px;
  font-weight: 600;
  margin-right: 20px;
}
.header .contacts .time {
  font-size: 14px;
  font-weight: 300;
  margin-right: 25px;
}
.header .contacts .callback {
  border-radius: 1000px;
  background-color: #f68a53;
  color: #fff;
  font-size: 14px;
  padding: 10px 16px;
  display: inline-block;
}
.header .header__bottom {
  display: flex;
  align-items: center;
  padding: 17px 0 16px;
}
.header .logo svg {
  width: 203px;
  height: 47px;
}
.header .btns {
  display: flex;
  align-items: center;
  margin-left: 23px;
}
.header .btns__item {
  padding: 12px 17px;
  border-radius: 6px;
  font-weight: 500;
  font-size: 15px;
  cursor: pointer;
  position: relative;
}
.header .btns__item:after {
  position: absolute;
  left: calc(50% - 4px);
  top: calc(100% + 11px);
  content: '';
  border: 8px solid transparent;
  border-top: 8px solid rgba(0, 21, 24, 0.651);
  opacity: 0;
  z-index: 6;
  transition: 0.1s;
}
.header .btns__item.active:after {
  opacity: 1;
  transition: 0.1s;
  transition-delay: 0.5s;
}
.header .btns__item.catalog-btn {
  background-color: #f68a53;
  color: #fff;
  margin-right: 5px;
  position: relative;
  padding-left: 47px;
}
.header .btns__item.catalog-btn svg {
  position: absolute;
  left: 14px;
  top: 11px;
  width: 23px;
  height: 16px;
  fill: #fff;
}
.header .btns__item.brands-btn {
  background-color: #4bc4d7;
  color: #fff;
}
.header .search-form {
  position: relative;
  border: 1px solid #d5d5d5;
  overflow: hidden;
  border-radius: 6px;
  margin-left: 6px;
  width: 100%;
  max-width: 430px;
}
.header .search-form__input {
  border: none;
  outline: none;
  width: 100%;
  font-size: 15px;
  font-weight: 400;
  color: #2c2d2b;
  font-family: 'Commissioner', sans-serif;
  padding: 10px 16px;
}
.header .search-form__input::placeholder {
  color: rgba(44, 45, 43, 0.4);
  font-family: 'Commissioner', sans-serif;
  font-size: 15px;
}
.header .search-form__submit {
  background-color: #f68a53;
  color: #fff;
  width: 34px;
  height: 34px;
  position: absolute;
  right: 2px;
  top: 2px;
  border: none;
  background: none;
  outline: none;
  border-radius: 4px;
  padding: 0;
}
.header .search-form__submit svg {
  width: 19px;
  height: 18px;
  fill: #fff;
}
.header .icons {
  display: flex;
  justify-content: flex-end;
  margin-left: auto;
}
.header .icons__item {
  text-align: center;
  position: relative;
  padding: 0 10px;
  margin: 0 9px;
}
.header .icons__item .label {
  position: absolute;
  right: -13px;
  top: -7px;
  background-color: #f68a53;
  border: 1px solid #fff;
  border-radius: 100px;
  color: #fff;
  font-size: 10px;
  font-weight: 700;
  padding: 2px 5px;
}
.header .icons__item .icon {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  margin: auto;
  margin-bottom: 5px;
}
.header .icons__item .text {
  font-size: 14px;
  font-weight: 500;
}
.header .icons__item.compare svg {
  width: 14px;
  height: 18px;
  fill: #2c2d2b;
}
.header .icons__item.cabinet svg {
  width: 18px;
  height: 20px;
  fill: #2c2d2b;
}
.header .icons__item.basket svg {
  width: 19px;
  height: 20px;
  fill: #2c2d2b;
}
.header .dropdown {
  position: absolute;
  top: calc(100% - 10px);
  width: 100%;
  left: 0;
  background-color: #fff;
  z-index: 5;
  padding: 39px 0 32px;
  display: none;
}
.header .dropdown__list {
  list-style: none;
  column-count: 4;
  column-gap: 32px;
  padding: 0;
  margin: 0;
  font-size: 14px;
}
.header .dropdown__list li {
  position: relative;
  padding-left: 22px;
  margin-bottom: 13px;
}
.header .dropdown__list li:before {
  content: '';
  position: absolute;
  left: 4px;
  top: 5px;
  width: 5px;
  height: 5px;
  border-radius: 100px;
  background-color: #4bc4d7;
}
.header .burger {
  position: relative;
  display: none;
  right: 0;
  top: 0;
  width: 25px;
  min-width: 25px;
  height: 17px;
  z-index: 6;
  border-radius: 100px;
  margin-left: 20px;
}
.header .burger:before {
  content: '';
  position: absolute;
  box-shadow: 0 0 0 0 #e0f2f5;
  transition: 1s;
  width: 2px;
  height: 2px;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  margin: auto;
  display: block;
  border-radius: 10000px;
}
.header .burger.active:before {
  box-shadow: 0 0 0 5000px #e0f2f5;
}
.header .burger.active span:nth-of-type(1) {
  bottom: 0;
  transform: rotate(-45deg);
}
.header .burger.active span:nth-of-type(2) {
  transform: rotate(45deg);
}
.header .burger.active span:nth-of-type(3) {
  top: auto;
  opacity: 0;
}
.header .burger span {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  width: 100%;
  height: 2px;
  background-color: #2c2d2b;
  display: block;
  border-radius: 100px;
  transition: 0.5s;
  transform: rotate(0);
  opacity: 1;
}
.header .burger span:nth-of-type(1) {
  bottom: 16px;
}
.header .burger span:nth-of-type(3) {
  top: 16px;
}
.header .mobile-menu {
  position: fixed;
  top: 70px;
  right: 0;
  height: calc(100vh - 70px);
  overflow: auto;
  width: 100%;
  z-index: 6;
  padding: 0 40px 40px;
  visibility: hidden;
  opacity: 0;
  transform: translateY(-20px);
  transition: 0.5s;
  transition-delay: 0.2s;
}
.header .mobile-menu.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}
.header .mobile-menu .nav {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0;
  padding: 0;
}
.header .mobile-menu .nav__item {
  color: #2c2d2b;
  padding: 5px 0;
  position: relative;
  font-size: 15px;
}
.header .mobile-menu .nav__item:hover {
  color: inherit;
}
.header .mobile-menu .nav__item:not(:last-of-type) {
  margin-right: 0;
}
.header .mobile-menu .nav__item.active {
  color: #f68a53;
}
.header .mobile-menu .nav__item.active:before {
  display: none;
}
.header .mobile-menu .search-form {
  display: block;
  margin-left: 0;
  margin-top: 10px;
}
.header .mobile-menu .contacts {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding-left: 0;
  margin-top: 40px;
  padding-top: 20px;
}
.header .mobile-menu .contacts .phone {
  font-size: 15px;
  color: #2c2d2b;
  margin: 0 0 10px;
}
.header .mobile-menu .contacts .time {
  font-size: 15px;
  color: #2c2d2b;
  margin: 0 0 10px;
}
.header .mobile-menu .contacts .callback {
  font-size: 15px;
}
.header .mobile-menu .btns {
  display: flex;
  margin: 20px 0;
}
.header .mobile-menu .btns__item {
  padding: 12px 17px;
  font-size: 15px;
}
.header .mobile-menu .btns__item.catalog-btn {
  background-color: #f68a53;
  color: #fff;
  padding-left: 47px;
}
.header .mobile-menu .btns__item.catalog-btn svg {
  left: 14px;
  top: 11px;
  width: 23px;
  height: 16px;
}
@media screen and (max-width: 1200px) {
  .header .nav {
    padding-right: 15px;
    border: none;
  }
  .header .nav__item {
    font-size: 12px;
  }
  .header .nav__item:not(:last-of-type) {
    margin-right: 15px;
  }
  .header .contacts {
    padding-left: 15px;
  }
  .header .contacts .phone {
    font-size: 12px;
  }
  .header .contacts .time {
    font-size: 12px;
    margin-right: 15px;
  }
  .header .contacts .callback {
    font-size: 12px;
    padding: 10px 14px;
  }
  .header .logo svg {
    width: 140px;
    height: 33px;
  }
  .header .btns__item {
    padding: 8px 12px;
    font-size: 12px;
  }
  .header .btns__item.catalog-btn {
    padding-left: 35px;
  }
  .header .btns__item.catalog-btn svg {
    left: 12px;
    width: 15px;
    height: 11px;
    top: 9px;
  }
  .header .search-form__input {
    font-size: 12px;
    padding: 6px 12px;
  }
  .header .search-form__input::placeholder {
    font-size: 12px;
  }
  .header .search-form__submit {
    width: 22px;
    height: 22px;
  }
  .header .search-form__submit svg {
    width: 13px;
    height: 12px;
  }
  .header .icons__item .text {
    font-size: 12px;
  }
}
@media screen and (max-width: 992px) {
  .header__top {
    display: none;
  }
  .header .logo svg {
    width: 125px;
    height: 29px;
  }
  .header .search-form {
    max-width: 250px;
  }
  .header .icons {
    margin-right: auto;
  }
  .header .icons__item {
    margin: 0;
  }
  .header .burger {
    display: block;
  }
}
@media screen and (max-width: 768px) {
  .header .search-form {
    display: none;
  }
  .header .icons {
    margin-right: 0;
  }
  .header .icons__item .icon {
    margin-bottom: 0;
  }
  .header .icons__item .text {
    display: none;
  }
}
@media screen and (max-width: 576px) {
  .header .btns {
    display: none;
  }
}
